import React, { useEffect, useState } from "react";
import { getStore } from "../stores/Utils";

import './tabsHRM.css'


const Tab6Indicator = () => {
	var els = document.querySelectorAll("[data-tab-item]");
	var listTabH = [],
		sumTH = 0;
	for (var ss = 0; ss < els.length; ss++) {
		var hh = els[ss].getBoundingClientRect().width;
		if (hh < 200) {
			listTabH.push({ l: sumTH, w: hh });
			sumTH += hh;
		}
	}
	this.setState({ widthList: listTabH });
};





const TABSHRM = ({ keyCode, pth, indexTab, data, updateIndex }) => {
	

	let _outerWidth = getStore("outerWidth");
	const [rowWidth, setRowWidth] = useState(false);
	const [index, setIndex] = useState(0);
	const [widthScreen, setWidthScreen] = useState(null);


	let _index_ = indexTab ? indexTab - 1 : index;

	useEffect(() => {
		if (widthScreen !== _outerWidth) {
			setWidthScreen(_outerWidth);
			TabIndicator();
		}
	});

	const TabIndicator = () => {
		var els = document.querySelectorAll("[data-tab-item]");
		var listTabH = [],
			sumTH = 0;
		for (var ss = 0; ss < els.length; ss++) {
			var hh = els[ss].getBoundingClientRect().width;
			if (hh < 200) {
			}
			listTabH.push({ l: sumTH, w: hh });
			sumTH += hh;
		}
		setRowWidth(listTabH);
	};

	const UpdateIndex = (i) => {
		setIndex(i);
		TabIndicator();
		if (typeof updateIndex === "function") {
			updateIndex(i);
		}
	};

	var indcatorSize = rowWidth ? rowWidth[_index_] : null;
	var tabIndicator = { width: `0px`, left: `0px` };
	if (indcatorSize) {
		tabIndicator = {
			width: `${indcatorSize.w}px`,
			left: `${indcatorSize.l}px`,
		};
	}

	
	
	return (
			<div
				className="c-tabs-nav "
				data-active-item="1"
				data-default-key="0"
				data-c-tabs-nav=""
				data-ca-category="device-tray">
				<div className="center_Tab_Nav_Items" role="tablist">
					{data &&
						data.map((tbI, i) => {
							var color = "var(--tab--nav-Color--)";
							var activeClass = "center_Tab_Nav_Item";
							if (_index_  === i) {
								color = "var(--color-base--hover)";
								activeClass = "center_Tab_Nav_Item is-active";
							}

							return (
								<div
									className={activeClass}
									key={`${tbI.key}---${i}`}>
									<div
										data-tab-item={i}
										className="presentation"
										onClick={() => UpdateIndex(tbI.key)}
										key={tbI.key}>
										<a
											className="center_Tab_Nav_Link"
											role="tab"
											data-tab-index={i}>
											{tbI.label}
										</a>
									</div>
								</div>
							);
						})}
				</div>
				<span
					id="header-tabs-nav__indicator"
					className="c-tabs-nav__indicator"
					style={tabIndicator}></span>
			</div>
	);
};

export default TABSHRM;
